import React from 'react'
import PropTypes from 'prop-types'
import { Title, TitleAccentLine } from '../Title'
import Input from '../Input'
import Divider from '../Divider'
import Cta from '../Cta'
import { Image } from '../Image'
import downloadIcon from '../../static/download-icon.png'
import "./download-form.scss";

function DownloadForm({ title, subtitle }) {
  return (
    <div className="download-form">
      <Title className="download-form__title" variant="form">
        {title}
        <TitleAccentLine variant="formBlack">{subtitle}</TitleAccentLine>
      </Title>
      <Divider variant="hidden" />
      <Input label="Name" required className="download-form__input" />
      <Input label="Email" required className="download-form__input" />
      <Cta as="button" variant="form" to="#">
        DOWNLOAD PROFILE{' '}
        <Image variant="cta" src={downloadIcon} alt="download icon" />
      </Cta>
    </div>
  )
}

DownloadForm.propTypes = {
  title: PropTypes.string.isRequired,
  subtitle: PropTypes.string,
}

export default DownloadForm
