import PropTypes from 'prop-types'
import React from 'react';
import {  GridContainer } from '../../components/Grid'
import { Title } from '../../components/Title'
import Cta from '../../components/Cta'
import { Image } from '../../components/Image'
import PlusImage from '../../static/plus.png'
import "./landing-hero.scss"

function LandingHero({ title, subtitle, cta }) {
  return (
    <GridContainer className="landing-hero">

          <Title
            className="landing-hero__title"
            variant="lightLead">
            {title}
          </Title>

          <p>{subtitle}</p>

          <Cta variant="primary" to={cta.to}>
            {cta.text} <Image variant="cta" src={PlusImage} alt="plus icon" />
          </Cta>
    </GridContainer>
  )
}

LandingHero.propTypes = {
  title: PropTypes.string,
  subtitle: PropTypes.string,
  cta: PropTypes.shape({
    to: PropTypes.string,
    text: PropTypes.string,
  }),
}

export default LandingHero
